const validate = values => {
  const errors = {}
  if (!values.full_street_address) {
    errors.full_street_address = 'Required'
  }
  if (!values.local) {
    errors.local = 'Required'
  }
  if (!values.birthday) {
    errors.birthday = 'Required'
  }
  if (!values.country) {
    errors.country = 'Required'
  }
  if (!values.phone) {
    errors.phone = 'Required'
  }
  if (!values.education) {
    errors.education = 'Required'
  }
  if (!values.job) {
    errors.job = 'Required'
  }
  if (!values.avatar) {
    errors.avatar = 'Required'
  }

  return errors
}

export default validate



